import Aux from "../../hoc/_ux";
import Classes from "./MovieContainer.module.css";
import MovieInfoComponent from "../MovieInfoComponent";
import MovieComponent from "../MovieComponent";

function MovieContainer(props) {
  return (
    <Aux>

      {/* Movie Details when clicked on a movie Start */}
      {/* {props.selectedMovie && (
        <MovieInfoComponent isLoading={props.isLoading} />
      )} */}
      {/* Movie Details when clicked on a movie Start */}
      
      <div className={Classes.MovieListContainer}>
        {!props.movieData.length ? (
          <div className={Classes.movieIcon}>
            <img src="./movie-icon.svg" alt="Movie_icon" />
          </div>
        ) : (
          props.movieData.map((movie, id) => (
            <MovieComponent
              key={id}
              movieData={movie}
              onShowMovie={props.onDisplayMovieDetails}
            />
          ))
        )}
      </div>
    </Aux>
  );
}

export default MovieContainer;
