import styled from "styled-components";
import CancelIcon from "@material-ui/icons/Cancel";
import { connect } from "react-redux";

const InfoComponent = styled.div`
  display: flex;
  flex-direction: row;
  padding: 20px;
  border-bottom: 1px solid #aaa;
  gap: 20px;
  @media (max-width: 700px) {
    flex-direction: column;
    gap: 0px;
    padding: 0px;
    img {
        display: none;
    }
  }
`;

const SubDetails = styled.div`
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 20px;
  @media (max-width: 700px){
      padding: 10px;
  }
`;
const SpanHeadDetail = styled.span`
  padding: 8px 8px 10px 8px;
  font-size: 20px;
  font-weight: bold;
`;

const SpanDetail = styled.span`
  padding: 8px;
  font-size: 15px;
  font-weight: bold;
  & span {
    opacity: 0.5;
    font-weight: normal;
  }
`;

const CancelButton = styled.div`
  display: block;

  @media (max-width: 700px) {
    display: none;
  }
`;

function MovieInfoComponent(props) {
  return (
    <>
      {props.isLoading ? (
        <div style={{ textAlign: "center" }}>Loading...</div>
      ) : (
        <InfoComponent>
          <img
            src={props.selectedMovie.Poster}
            alt={props.selectedMovie.Title}
          />
          <SubDetails>
            <SpanHeadDetail>Movie: {props.selectedMovie.Title}</SpanHeadDetail>
            <SpanDetail>
              IMDB Rating: <span>{props.selectedMovie.imdbRating}</span>
            </SpanDetail>
            <SpanDetail>
              Year: <span>{props.selectedMovie.Year}</span>
            </SpanDetail>
            <SpanDetail>
              Language: <span>{props.selectedMovie.Language}</span>
            </SpanDetail>
            <SpanDetail>
              Rated: <span>{props.selectedMovie.Rated}</span>
            </SpanDetail>
            <SpanDetail>
              Released: <span>{props.selectedMovie.Released}</span>
            </SpanDetail>
            <SpanDetail>
              Runtime: <span>{props.selectedMovie.Runtime}</span>
            </SpanDetail>
            <SpanDetail>
              Genre: <span>{props.selectedMovie.Genre}</span>
            </SpanDetail>
            <SpanDetail>
              Director: <span>{props.selectedMovie.Director}</span>
            </SpanDetail>
            <SpanDetail>
              Actors: <span>{props.selectedMovie.Actors}</span>
            </SpanDetail>
            <SpanDetail>
              Plot: <span>{props.selectedMovie.Plot}</span>
            </SpanDetail>
          </SubDetails>
          <CancelButton>
            <CancelIcon
              style={{ cursor: "pointer" }}
              onClick={props.setMovieDetails}
            />
          </CancelButton>
        </InfoComponent>
      )}
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    selectedMovie: state.selectedMovie,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    setMovieDetails: () => {
      dispatch({ type: "SET_MOVIE_DETAILS", movieDetails: "" });
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(MovieInfoComponent);
