import Classes from "./App.module.css";
import axios from "axios";
import { useState } from "react";
import Layout from "./hoc/Layout";
import { useSelector, useDispatch } from "react-redux";
import Modal from "./component/UI/Modal/Modal";
import MovieContainer from "./component/MovieContainer/MovieContainer";
import MovieInfoComponent from "./component/MovieInfoComponent";

const APIKEY = "eada3e85";

function App() {
  const [movieData, setMovieData] = useState([]);
  const [loading, setLoading] = useState(false);

  const state = useSelector((state) => state);
  const dispatch = useDispatch();

  const fetchData = async (queryString) => {
    if (queryString) {
      try {
        const res = await axios.get(
          `https://www.omdbapi.com/?s=${queryString}&apikey=${APIKEY}`
        );
        if (res.data.Response === "True") {
          setMovieData(res.data.Search);
        } else {
          setMovieData([]);
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  const cancelMovieDetails = () => {
    dispatch({ type: "SET_MOVIE_DETAILS", movieDetails: "" });
  };

  const displayMovieDetails = async (id) => {
    setLoading(true);
    let movieDetail = await axios.get(
      `https://www.omdbapi.com/?i=${id}&apikey=${APIKEY}`
    );
    // onMovieSelect(movieDetail.data);
    dispatch({ type: "SET_MOVIE_DETAILS", movieDetails: movieDetail.data });
    setLoading(false);
  };

  return (
    <div className={Classes.container}>
      <Layout fetchData={fetchData}>
        <Modal show={state.selectedMovie} modalClosed={cancelMovieDetails}>
          {state.selectedMovie && <MovieInfoComponent isLoading={loading} />}
        </Modal>
        <MovieContainer
          selectedMovie={state.selectedMovie}
          isLoading={loading}
          movieData={movieData}
          onDisplayMovieDetails={displayMovieDetails}
        />
      </Layout>
    </div>
  );
}

export default App;
